import React, { Component } from 'react';

class Card extends Component {
  render() {
    return(
      <div className='card'>
        <a className='card-link' href={this.props.projLink} target='_blank'>
          <img className='card-image' src={this.props.projImg} alt={this.props.projAlt}/>
          <p className='card-title'>{this.props.projTitle}</p>
        </a>
      </div>
    );
  }
}

export default Card;
